/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiButton from '@mui/material/Button'
import { selector, atom, useRecoilState } from 'recoil'
import { NAMCResource, GrantAction } from '@namcbugdb/common'

import { ReadOnlyField } from '../ReadOnlyField'
import { CheckboxField } from '../CheckboxField'
import { TextField } from '../TextField'
import { AutocompleteField } from '../AutocompleteField'
import { Box, MyUser } from '../../types'
import { BoxSamples } from './BoxSamples'
import { useComponentController } from './componentController'
import { selectBoxStates, selectOrganizations } from '../../data/selectors'
import { AccordionPanel } from '../AccordionPanel'
import { Title } from '../Title'
import { formatUtcDateTimeToAppDate } from '../../lib/formats'
import { SortingProgressField } from './SortingProgressField'
import { IdentificationProgressField } from './IdentificationProgressField'
import { ModelsField } from './ModelsField'
import { ConfirmNavigateAway } from '../AppRouter'
import { Menu } from '../Menu'
import { downloadBoxTaxaReport } from './boxDetailReports'
import { useApi } from '../../data/api'
import { useNotifications } from '../AppNotifications'
import { BoxSitesMapPreview } from './BoxSitesMapPreview'

const selectOrganizationOptions = selector({
  key: 'BoxDetail/selectOrganizationOptions',
  get: async ({ get }) => {
    const organizations = await get(selectOrganizations)
    return organizations.map(({ id: value, name: label }) => ({ value, label }))
  },
})

const selectBoxStateOptions = selector({
  key: 'BoxDetail/selectBoxStateOptions',
  get: async ({ get }) => {
    const boxStates = await get(selectBoxStates)
    return boxStates.map(({ id, name }) => ({ value: id, label: name }))
  },
})

const isExpandedState = atom<boolean>({
  key: 'BoxDetail/isExpandedState',
  default: true,
})

export interface BoxDetailProps {
  box: Box
  user: MyUser
}

export const BoxDetail = ({ box, user }: BoxDetailProps): JSX.Element => {
  const api = useApi()
  const notifications = useNotifications()

  const [state, actions] = useComponentController({ box })
  const [isPanelExpanded, setIsPanelExpanded] = useRecoilState(isExpandedState)
  const {
    editMode,
    values: { boxStateId, measurements, midges, description, customerId, alias, modelIds },
    isDirty,
  } = state
  const { setFieldValue } = actions
  const isEditing = editMode === 'started'

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <AccordionPanel
          value={isPanelExpanded}
          onChange={setIsPanelExpanded}
          locked={isEditing}
          summary={
            <Title>
              Box {box.id}{' '}
              <Title.Subtitle>
                ({box.boxState}, {box.sampleCount} samples)
              </Title.Subtitle>
            </Title>
          }
          actions={
            <div css={{ display: 'flex', gap: '0.5em' }}>
              <Menu
                label="Reports"
                variant="outlined"
                disabled={editMode !== 'stopped'}
                items={[
                  {
                    key: 'boxTaxonomyReport',
                    label: 'Box Taxonomy Report',
                    onClick: async () => {
                      try {
                        const cancelNotification = notifications.info('Still creating report...', 1000)
                        await downloadBoxTaxaReport({
                          api,
                          boxId: box.id,
                          filename: `Box Taxonomy Report for Box ID ${box.id}`,
                        })
                        cancelNotification()
                      } catch (err) {
                        notifications.error(`Failed to create box taxonomy report: ${err}`)
                      }
                    },
                  },
                ]}
              />
              {editMode === 'stopped' && user.hasPermission(NAMCResource.boxes, GrantAction.updateAny) && (
                <MuiButton
                  onClick={() => {
                    actions.startEdit()
                    setIsPanelExpanded(true)
                  }}
                  variant="outlined"
                >
                  Edit
                </MuiButton>
              )}
              {(editMode === 'started' || editMode === 'stopping') && (
                <>
                  <MuiButton
                    onClick={() => {
                      actions.stopEdit(true)
                    }}
                    variant="outlined"
                    disabled={editMode === 'stopping'}
                  >
                    Cancel
                  </MuiButton>
                  <MuiButton
                    onClick={() => {
                      actions.stopEdit()
                    }}
                    variant="contained"
                    disabled={!isDirty || editMode === 'stopping'}
                  >
                    Save
                  </MuiButton>
                </>
              )}
            </div>
          }
        >
          <div css={{ display: 'flex', gap: '1em' }}>
            <div
              css={{
                flex: 1,
                display: 'grid',
                gap: '0.75em',
                gridTemplateColumns: 'auto auto auto auto',
                gridTemplateAreas: `
              "boxState     customerId              sorting       identification"
              "measurements submittedByName         modelIds      modelIds      "
              "midges       createdDate             modelIds      modelIds      "
              "taxaQa       receivedDate            alias         alias         "
              "sorterQa     processingCompleteDate  description   description   "
              ".            .                       description   description   "
            `,
              }}
            >
              <AutocompleteField
                label="Box State"
                optionsSelector={selectBoxStateOptions}
                value={boxStateId}
                valueLabel={box.boxState}
                onChange={(value) => setFieldValue('boxStateId', value)}
                readOnly={!isEditing}
                gridArea="boxState"
              />
              <AutocompleteField
                gridArea="customerId"
                optionsSelector={selectOrganizationOptions}
                valueLabel={box.customerName}
                value={customerId}
                onChange={(value) => setFieldValue('customerId', value)}
                label="Customer"
                readOnly={!isEditing}
                css={{ minWidth: '20em' }}
              />
              <ReadOnlyField gridArea="submittedByName" value={box.submittedByName} label="Submitted By" />
              <ReadOnlyField
                gridArea="createdDate"
                value={formatUtcDateTimeToAppDate(box.createdDate ?? null)}
                label="Submitted On"
              />
              <ReadOnlyField
                gridArea="receivedDate"
                value={formatUtcDateTimeToAppDate(box.receivedDate)}
                label="Received On"
              />
              <ReadOnlyField
                gridArea="processingCompleteDate"
                value={formatUtcDateTimeToAppDate(box.processingCompleteDate ?? null)}
                label="Processing Completed On"
              />
              <CheckboxField
                label="Measurements"
                value={measurements}
                onChange={(value) => setFieldValue('measurements', value)}
                readOnly={!isEditing}
                gridArea="measurements"
              />
              <CheckboxField
                label="Midges"
                value={midges}
                onChange={(value) => setFieldValue('midges', value)}
                readOnly={!isEditing}
                gridArea="midges"
              />
              <ReadOnlyField label="Taxonomic QA" value={box.taxaQa} gridArea="taxaQa" />
              <ReadOnlyField label="Sorting QA" value={box.sorterQa} gridArea="sorterQa" />
              <ModelsField
                label="Models"
                value={modelIds}
                onChange={(value) => setFieldValue('modelIds', value)}
                readOnly={!isEditing}
                gridArea="modelIds"
              />
              <TextField
                label="Alias"
                value={alias}
                onChange={(value) => setFieldValue('alias', value)}
                readOnly={!isEditing}
                gridArea="alias"
              />
              <TextField
                label="Description"
                value={description}
                onChange={(value) => setFieldValue('description', value)}
                readOnly={!isEditing}
                multiline
                minRows={3}
                gridArea="description"
              />
              <SortingProgressField boxId={box.id} value={box.sortComplete} gridArea="sorting" />
              <IdentificationProgressField
                boxId={box.id}
                value={box.identificationComplete}
                gridArea="identification"
              />
            </div>
            <div>
              <BoxSitesMapPreview boxId={box.id} />
            </div>
          </div>
        </AccordionPanel>
        <div css={{ fontWeight: '500', marginLeft: '1em' }}>Samples</div>
        <div css={{ flex: 1, minHeight: '10em' }}>
          <BoxSamples boxId={box.id} />
        </div>
      </div>
      {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
    </>
  )
}
